.App {
  text-align: center;
  box-sizing: border-box;
  height: 90vh;
}

.Header-container {
  padding: 30px;
}

.Main-content {
  height: 80vh;
  display: grid;
  grid-template-columns: 1fr repeat(3, 2fr) 1fr;
  grid-template-rows: repeat(9, 1fr);
  grid-column-gap: 2px;
  grid-row-gap: 10px;
  @media (min-width: 961px) {
    grid-template-columns: 1fr 2fr repeat(6, 1fr);
  }
}

.Footer {
  padding-top: 50px;
}
